section > h1
  font-size: 2em
  font-weight: bold
  color: #000
  text-align: center

article
  border-radius: 10px
  background-color: #fff
  box-shadow: 0 0 10px #000
  margin: 10px
  padding: 10px
  display: inline-block
  width: 20%
  text-align: center

nav > ul
  list-style-type: none
  padding: 0
  margin: 0
  text-align: center

nav > ul > li
  display: inline-block
  margin: 0 10px
  padding: 0 10px
  box-shadow: 0 0 10px #000
  border-radius: 10px

nav > ul > li > a
  color: #000
  text-decoration: none
  font-size: 1.5em
  font-weight: bold
  padding: 10px

header > p, footer
  color: #000
  text-align: center
  font-size: 1.5em
  font-weight: bold

img
  width: 100px
  height: 100px

body
  background-color: #eee

header > p, footer
  background-color: chocolate

